/* base
-------------------------------------------------- */
/*----------  css reset  ----------*/
/* http://meyerweb.com/eric/tools/css/reset/ 
	v2.0 | 20110126
	License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

/*----------  css reset  ----------*/
/*----------  reboot  ----------*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  font-family: PingFang TC , BlinkMacSystemFont ,  "Segoe UI" ,  '微軟正黑體', "Microsoft JhengHei" , "Helvetica Neue" ,  Arial ,  sans-serif;
  font-size: 1rem;
  line-height: 1.5;
  background-color: #FFFFFF;
  color: #000000;
}

h1, h2, h3, h4, h5 {
  line-height: 1.2;
}

img {
  vertical-align: bottom;
  max-width: 100%;
  height: auto;
}

a {
  text-decoration: none;
}

::selection {
  background: #650300;
  color: #FFFFFF;
}

/*----------  reboot  ----------*/
/* End of base
-------------------------------------------------- */
/* layout
-------------------------------------------------- */
/*----------  grid  ----------*/
/* container */
.l-container {
  margin: 0 auto;
  width: 100%;
  padding-left: 6px;
  padding-right: 6px;
}

@media (min-width: 376px) {
  .l-container {
    padding-left: 12px;
    padding-right: 12px;
  }
}

@media (min-width: 768px) {
  .l-container {
    max-width: 696px;
  }
}

@media (min-width: 1580px) {
  .l-container {
    max-width: 1224px;
  }
}

.l-container--full {
  padding-left: 6px;
  padding-right: 6px;
}

@media (min-width: 376px) {
  .l-container--full {
    padding-left: 12px;
    padding-right: 12px;
  }
}

/* /container */
/* row */
.l-row {
  display: flex;
  flex-wrap: wrap;
  margin-left: -6px;
  margin-right: -6px;
}

@media (min-width: 376px) {
  .l-row {
    margin-left: -12px;
    margin-right: -12px;
  }
}

/* /row */
